// import Vue from 'vue'
// import VueRouter from 'vue-router' 
// Vue.use(VueRouter)
import { createRouter, createWebHistory } from 'vue-router';
import layout from '@/views/layout/default/Index.vue';
//import load from './pub/loadType'
import { getJava } from './pub/backend/java.js';
import { getPhp } from './pub/backend/php.js';
import { getNode } from './pub/backend/node.js';

const routes = [
  {
    path: '/',
    name: 'home',
    component: layout,
    children: [
      {
        path: '',
        name: 'main',
        component: () => import('@/views/page/Main.vue'), // 해당 페이지 방문 시 vue 파일을 로드함.
        props: true
    }
    ]
  },
  {
    path: '/stieView/',
    name: 'stieView',
    component: layout,
    children: [
      {
        path: '',
        name: 'stieView',
        component: () => import('@/views/page/blog/site/test/stieView.vue'), // 해당 페이지 방문 시 vue 파일을 로드함.
        props: true
     }
    ]
  },
  {
    path: '/crawlingView/',
    name: 'crawlingView',
    component: layout,
    children: [
      {
        path: '',
        name: 'crawlingView',
        component: () => import('@/views/page/blog/site/test/crawlingView.vue'), // 해당 페이지 방문 시 vue 파일을 로드함.
        props: true
     }
    ]
  },
  getJava(layout),
  getPhp(layout),
  getNode(layout),

  // 추가 라우트를 여기에 정의합니다.
]

// 라우터 인스턴스 생성
const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router
