import load from '../../loadType.js' 

const getPhp = (layout) => {
	return   {
    path: '/php/',
    name: 'php',
    component: layout,
    children: [
      {
        path: 'no',
        name: 'no1',
        component:  load.php("no.vue"),
        props: true,
      },
    ]
  }
}


export { getPhp }