import getload from '../../loadType.js' 
import getName from '../../nameType.js' 


const getJava = (layout) => {
return  {
    path: '/java/',
    name: 'java',
    component: layout,
    children: [
      {
        path: '',
        name: getName.java("null"),
        component: getload.java("no.vue"),
        props: true,
      },
    ]
  }
}


export { getJava }