import load from '../../loadType.js' 

const getNode = (layout) => {
	return   {
    path: '/node/',
    name: 'node',
    component: layout,
    children: [
      {
        path: 'no',
        name: 'no1',
        component:  load.node("no.vue"),
        props: true,
      },
    ]
  }
}


export { getNode }